html,
body {
  overflow-x: hidden;
  background: #edf0f5;
}

a {
  outline: 0;
}

#site {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

.site-wrapper {
  flex: 1;
  position: relative;
  top: 0;
  left: 0;
  margin: 0;
  padding: 60px 0 0 0;
  overflow: hidden;
  z-index: 250;
  transition: transform .5s;
}

body.mobile-menu-open .site-wrapper {
  transform: translateX(265px);
}

body.mobile-menu-open .site-footer {
  transform: translateX(265px);
}

.main-content {
  flex: 1 0 auto;
  background: #edf0f5;
}

.content {
  padding: 40px 0;
}

@media screen and (min-width: 992px) {
  .content.smaller-content {
    padding: 40px 150px;
  }
}

.white-container {
  background: #fff;
  box-shadow: 0 7px 14px 0 rgba(50, 50, 93, 0.1), 0 3px 6px 0 rgba(0, 0, 0, 0.07);
  padding: 20px 15px;
  margin-bottom: 15px;
  border-radius: 4px;
}

@media screen and (min-width: 992px) {
  .white-container {
    margin-bottom: 0;
  }
}

.contentSection-heading {
  font-size: 16px;
}

@media screen and (min-width: 992px) {
  .main-area-with-sidebar {
    display: flex;
  }
}

.main-area-with-sidebar .main-area {
  margin-bottom: 30px;
}

@media screen and (min-width: 992px) {
  .main-area-with-sidebar .main-area {
    flex: 2;
    margin-bottom: 0;
  }
}

@media screen and (min-width: 1200px) {
  .main-area-with-sidebar .main-area {
    flex: 3;
  }
  .main-area-with-sidebar .main-area.large-sidebar {
    flex: 2 !important;
  }
}

@media screen and (min-width: 992px) {
  .main-area-with-sidebar .main-area-sidebar {
    flex: 1;
    margin-left: 30px;
  }
  .main-area-with-sidebar .main-area-sidebar.left {
    margin-left: 0;
    margin-right: 30px;
  }
}

.white-contentSection {
  box-shadow: 0 7px 14px 0 rgba(50, 50, 93, 0.1), 0 3px 6px 0 rgba(0, 0, 0, 0.07);
  background: #fff;
  border-radius: 4px;
}

.white-contentSection .white-contentSection-header {
  padding: 15px 20px;
}

.white-contentSection .white-contentSection-header.is-gray {
  background: #f6f9fc;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}

.white-contentSection .white-contentSection-header.has-link h3 {
  margin-bottom: 5px;
}

@media screen and (min-width: 768px) {
  .white-contentSection .white-contentSection-header.has-link {
    display: flex;
    align-items: center;
  }
  .white-contentSection .white-contentSection-header.has-link h3 {
    flex: 2;
    margin-bottom: 0;
  }
  .white-contentSection .white-contentSection-header.has-link a {
    flex: 1;
    text-align: right;
    font-size: 13px;
  }
}

.white-contentSection .white-contentSection-header.has-actions {
  display: flex;
}

.white-contentSection .white-contentSection-header.has-actions .header-content {
  flex: 2;
}

@media screen and (min-width: 768px) {
  .white-contentSection .white-contentSection-header.has-actions .header-content {
    flex: 3;
  }
}

.white-contentSection .white-contentSection-header.has-actions .header-actions {
  flex: 1;
  align-self: flex-start;
  justify-content: flex-end;
}

.white-contentSection .white-contentSection-header h3 {
  font-size: 16px;
  font-weight: 500;
  margin: 0;
}

.white-contentSection .gray-contentSection-content {
  background: #f6f9fc;
  border-top: 1px solid #e6ebf1;
  padding: 15px 20px;
}

.white-contentSection .white-contentSection-content {
  border-top: 1px solid #e6ebf1;
  padding: 20px 20px;
}

.white-contentSection .white-contentSection-footer {
  padding: 15px;
  display: flex;
  justify-content: flex-end;
  background: #f6f9fc;
  border-top: 1px solid #e6ebf1;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.transparent-contentSection .transparent-contentSection-header {
  padding: 15px 0;
  border-bottom: 1px solid #cfd2d7;
}

.transparent-contentSection .transparent-contentSection-header h3 {
  font-size: 16px;
  font-weight: 500;
  margin: 0;
}

.transparent-contentSection .transparent-contentSection-header.has-link {
  display: flex;
  align-items: center;
}

.transparent-contentSection .transparent-contentSection-header.has-link h3 {
  flex: 2;
}

.transparent-contentSection .transparent-contentSection-header.has-link a {
  flex: 1;
  text-align: right;
  font-size: 13px;
}

.transparent-contentSection .transparent-contentSection-content {
  padding: 15px 0;
}

.items-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.items-list li {
  display: flex;
  padding: 6px 15px;
}

.items-list li.gray-item {
  background: #f4f8fb;
  border-bottom: 1px solid #e8ecef;
}

.items-list li.white-item {
  background: #fff;
  border-bottom: 1px solid #e8ecef;
}

.items-list li .item-content {
  flex: 8;
}

.items-list li .item-content .item-header {
  font-weight: 500;
  font-size: 15px;
}

.items-list li .item-content .item-sub-data {
  color: #97A9B5;
}

.items-list li .item-go-to {
  flex: 1;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.items-list-default {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.items-list-default li {
  margin-bottom: 25px;
}

.items-list-default li.has-left-border {
  padding-left: 15px;
  border-left: 2px solid;
}

.items-list-default li.has-left-border.info {
  border-color: #8eb4cb;
}

.items-list-default li.has-left-border.warning {
  border-color: #cbb956;
}

.items-list-default li.has-left-border.success {
  border-color: #2ab27b;
}

.items-list-default li.has-left-border.critical {
  border-color: #cc0000;
}

.items-list-icon {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.items-list-icon li {
  display: flex;
  margin-bottom: 15px;
  padding: 10px 0;
  position: relative;
}

.items-list-icon li.has-go-to {
  cursor: pointer;
}

.items-list-icon li.has-go-to:after {
  content: '';
  width: 15px;
  height: 15px;
  position: absolute;
  top: calc(50% - 7.5px);
  right: 0;
  mask-image: url("../svg/heroicons-ui/svg/icon-cheveron-right.svg");
  mask-position: center;
  mask-repeat: no-repeat;
  background-color: #97A9B5;
}

.items-list-icon li.has-go-to:hover .icon {
  background-color: #607786;
}

.items-list-icon li.has-go-to:hover:after {
  background-color: #607786;
}

.items-list-icon li.has-go-to.exact-active {
  cursor: default;
}

.items-list-icon li.has-go-to.exact-active .icon {
  background-color: #009999;
}

.items-list-icon li.has-go-to.exact-active .item-list-icon-content {
  color: #282c2f;
  font-weight: 500;
}

.items-list-icon li.has-go-to.exact-active:after {
  background-color: #97A9B5;
}

.items-list-icon li .item-list-icon {
  flex: 1;
}

.items-list-icon li .item-list-icon-content {
  padding-left: 5px;
  flex: 4;
}

.site-footer {
  flex-shrink: 0;
  background-color: #313a45;
  padding: 50px 0 25px 0;
  transition: transform .5s;
}

.site-footer .footer-callouts {
  display: flex;
  align-items: flex-start;
  margin: 0 -15px;
}

.site-footer .footer-callouts .f__callout {
  flex: 1;
  padding: 0 15px;
}

.site-footer .footer-callouts .f__callout.footer-brand {
  display: none;
}

@media screen and (min-width: 768px) {
  .site-footer .footer-callouts .f__callout.footer-brand {
    display: block;
    flex: 2;
  }
}

.site-footer .footer-callouts .f__callout .f__callout-heading {
  color: #fff;
  padding-bottom: 10px;
  margin-bottom: 10px;
  font-weight: normal;
  text-transform: uppercase;
  letter-spacing: 2px;
  border-bottom: 1px solid #687079;
}

.site-footer .footer-callouts .f__callout .f__links {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.site-footer .footer-callouts .f__callout .f__links li {
  margin-bottom: 5px;
}

.site-footer .footer-callouts .f__callout .f__links li a {
  color: #8b96a3;
  text-decoration: none;
  font-size: 13px;
}

.site-footer .footer-callouts .f__callout .f__links li a:hover {
  color: #009999;
}

.site-footer .footer-callouts .f__callout .f__social-media-link {
  margin: 10px 0 0 0;
  padding: 0;
  list-style-type: none;
}

.site-footer .footer-callouts .f__callout .f__social-media-link li {
  float: left;
  padding-right: 10px;
}

.site-footer .footer-callouts .f__callout .contact-container {
  color: #8b96a3;
  font-size: 13px;
}

.site-footer .footer-callouts .f__callout .contact-container a {
  color: #009999;
}

.site-footer .footer-callouts .f__callout .contact-container a:hover {
  color: #00caca;
}

body {
  font-family: "Montserrat", sans-serif;
  color: #636b6f;
  line-height: 1.6;
  font-size: 14px;
}

.is-italic, .is-bold-italic {
  font-style: italic !important;
}

.is-bold, .is-bold-italic {
  font-weight: bold !important;
}

.is-weight-500 {
  font-weight: 500 !important;
}

.is-small-text {
  font-size: 13px;
}

.is-extra-small-text {
  font-size: 12px !important;
}

.is-large-text {
  font-size: 18px;
}

.warning-color {
  color: #8a6d3b;
}

.gray-text {
  color: #97A9B5;
}

.gray-sub-text, .notification-settings-list li .notification-setting-content {
  font-size: 13px;
  color: #97A9B5;
}

.page-heading {
  color: #009999;
}

.btn-modern {
  border-radius: 3px;
  text-transform: uppercase;
  letter-spacing: 1.2px;
  line-height: 1.5;
  font-size: 15px;
  font-weight: 500;
  padding: .5em 1.5em;
  box-shadow: 0 1px 2px 0 rgba(74, 144, 255, 0.44), 0 2px 8px rgba(0, 0, 0, 0.14);
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.18);
  border: none;
}

.btn-modern:hover {
  text-decoration: none !important;
}

.btn-modern.primary {
  background-image: linear-gradient(-180deg, #00caca 0%, #009999 100%);
  color: #FFFFFF;
}

.btn-modern.primary:hover {
  background: #009999;
}

.processing {
  color: transparent !important;
  text-shadow: none !important;
  position: relative;
}

.processing:after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 20px;
  height: 20px;
  margin: -10px;
  border-radius: 50%;
  border-top: 2px solid transparent;
  border-left: 2px solid transparent;
  border-bottom: 2px solid rgba(255, 255, 255, 0.9);
  border-right: 2px solid rgba(255, 255, 255, 0.9);
  -webkit-animation: rotate 0.8s linear infinite;
  -moz-animation: rotate 0.8s linear infinite;
  -ms-animation: rotate 0.8s linear infinite;
  -o-animation: rotate 0.8s linear infinite;
  animation: rotate 0.8s linear infinite;
}

.btn {
  border-radius: 3px;
  padding: 5px 14px;
  font-weight: 500;
  border: none;
  box-shadow: 0 7px 14px 0 rgba(50, 50, 93, 0.1), 0 3px 6px 0 rgba(0, 0, 0, 0.07);
}

.btn:focus, .btn:active {
  outline: none !important;
}

.btn.active {
  box-shadow: 0 7px 14px 0 rgba(50, 50, 93, 0.1), 0 3px 6px 0 rgba(0, 0, 0, 0.07);
}

.btn-primary {
  background-color: #009999;
}

.btn-primary:hover {
  background-color: #00caca !important;
}

.btn-primary.active, .btn-primary:focus, .btn-primary:active, .btn-primary:active:focus {
  background-color: #009999;
  border: 0;
}

.btn-primary-bordered {
  border: 2px solid #009999;
  border-radius: 0;
