form.loading {
  position: relative;
  pointer-events: none;
  cursor: default;
}

form.loading:before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 100;
  background: rgba(255, 255, 255, 0.8);
}

form.loading:after {
  position: absolute;
  content: "";
  top: 150px;
  left: 50%;
  margin: -1.5em 0 0 -1.5em;
  width: 3em;
  height: 3em;
  border-radius: 500rem;
  border-style: solid;
  border-width: 0.2em;
  border-color: #009999 rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.1);
  box-shadow: 0 0 0 1px transparent;
  visibility: visible;
  z-index: 101;
  animation: form-spin .6s linear;
  -webkit-animation: form-spin .6s linear;
  animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite;
}

@keyframes form-spin {
  from {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

